import React from "react"
import { ListProps, List, useListContext, Pagination, useTranslate, FunctionField, Record, TextField } from "react-admin"
import { TableContainer, Table, TableHead, TableBody, TableRow, TableCell, useMediaQuery, useTheme } from "@material-ui/core"
import { useHistory } from "react-router"
import { MobilePostPagination } from '../common/Pagination'
import { ResourceName } from "../../core/ResourceName"

const BookingGrid = () => {
  const { data, ids } = useListContext()
  const history = useHistory()
  const translate = useTranslate()
  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>{translate(`resources.bookings.fields.eventId`)}</TableCell>
            <TableCell>{translate(`resources.bookings.fields.id`)}</TableCell>
            <TableCell>{translate(`resources.bookings.fields.variantName`)}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map(id =>
            <TableRow key={id} style={{ cursor: "pointer" }}>
              <TableCell onClick={() =>
                history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${id}/show`, })
              }>
                <FunctionField record={data[id]} render={(record: Record | undefined) => (
                  `${record?.eventId}. ${record?.eventName} ${record?.eventDateStart.replace("T", " ").substring(0, 19)}`
                )} />
              </TableCell>
              <TableCell onClick={() =>
                history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${id}/show`, })
              }>
                <TextField record={data[id]} source="id" />
              </TableCell>
              <TableCell onClick={() =>
                history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${id}/show`, })
              }>
                <TextField record={data[id]} source="variantName" />
              </TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const BookingList = (props: ListProps) => {
  const theme = useTheme()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={
        useMediaQuery(theme.breakpoints.up('md')) ? (
          <Pagination rowsPerPageOptions={[20, 50, 100]} />
        ) : (
          <MobilePostPagination />
        )
      }
      sort={{ field: 'id', order: 'DESC' }}
      {...props}
    >
      <BookingGrid />
    </List>
  )
}