import { DataProvider, UpdateParams, UpdateResult, GetListResult } from 'react-admin'
import { getByConditions, getWithPagination, patch } from '../common/fetch.utils'
import { BOOKINGS_URL, EVENTS_URL, VARIANTS_URL } from '../api-urls'
import { BookingUpdateRequest } from './booking-update.request'
import { BookingDto } from '../dto/booking.dto'
import { BookingListParams } from './booking-list.params'
import { getFilterMapper } from './booking-filter.mapper'
import { mapSortBookingParam } from './booking-sort.mapper'
import { Booking } from './booking'
import { EventDto } from '../dto/event.dto'
import { mapToBookings } from './bookings.mapper'
import { VariantDto } from '../dto/variant.dto'

const getFilterParams = (filter: Partial<BookingDto>) => {
  if (Object.keys(filter).length) {
    const filterParams = Object.entries(filter).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof BookingDto)
      return mapper("b", value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return "true"
}

const provider = {
  update: async (
    resource: string,
    { data }: UpdateParams<BookingUpdateRequest>,
  ): Promise<UpdateResult<BookingDto>> => {
    const booking = await patch<BookingUpdateRequest, BookingDto>(
      BOOKINGS_URL,
      data,
    )
    return Promise.resolve({
      data: booking,
    })
  },
  getList: async (
    resource: string,
    {
      filter,
      sort: { field, order },
      pagination: { page, perPage },
    }: BookingListParams
  ): Promise<GetListResult<Booking>> => {
    const filterParams = `b=>${getFilterParams(filter).toString()}`
    const queryParams: string [] = []
    queryParams.push(`sortBy=${mapSortBookingParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
    } = await getWithPagination<BookingDto[]>(`${BOOKINGS_URL}/GetByConditions`, path)

    const eventIds = data.map((booking) => booking.eventId)
    const events = await getByConditions<EventDto>(EVENTS_URL, `e=>"${eventIds.join(",")}".Contains(e.Id.ToString())`)

    const variantIds = data.map((booking) => booking.variantId)
    const variants = await getByConditions<VariantDto>(VARIANTS_URL, `v=>"${variantIds.join(",")}".Contains(v.Id.ToString())`)

    return {
      data: data.map((booking) => mapToBookings(booking, events, variants)),
      total: data.length
    }
  }
} as DataProvider

export default provider
