import { AccessDateDto } from "../dto/access-date.dto"

const mapping: Partial<Record<keyof AccessDateDto, string>> = {
  id: 'au=>au.Id',
  activeFrom: 'au=>au.ActiveFrom',
  activeTo: 'au=>au.ActiveTo',
  type: 'au=>au.Type'
}

export const mapSortAccessDatesParam = (key: keyof AccessDateDto) => mapping[key]