import React from 'react'
import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core'
import { Show, ShowProps, useTranslate, ArrayField, Datagrid, TextField } from 'react-admin'

const useStyles = makeStyles({
  noBottomBorder: {
    borderBottom: 'none',
  },
  quarterWidth: {
    width: '25%',
  },
})

const ViewTitle = (record: any) => {
  const {
    record: { eventName, date, id },
  } = record
  const t = useTranslate()
  const description = `${eventName} ${date.replace('T', ' ')} ${id}`
  return (
    <span>{`${t('resources.stats-overall-view.name')}: ${description}`}</span>
  )
}

const BlockGrid = (props: any) => {
  const {
    record: {
      soldAbo, soldInfix, soldNormal, soldPoints, soldService
    }
  } = props
  const classes = useStyles()
  const translate = useTranslate()

  const firstColumnEntries = [
    { label: "abo", value: soldAbo },
    { label: "infix", value: soldInfix },
    { label: "normal", value: soldNormal },
    { label: "points", value: soldPoints },
    { label: "service", value: soldService },
  ].map(({ label, value }) => (
    !!value && (
      <div key={label}>{translate(`resources.stats-overall-view.fields.${label}`)}</div>
    )
  ))

  const secondColumnEntries = [
    { label: "enteredAbo", value: soldAbo },
    { label: "enteredInfix", value: soldInfix },
    { label: "enteredNormal", value: soldNormal },
    { label: "enteredPoints", value: soldPoints },
    { label: "enteredService", value: soldService },
  ].map(({ label, value }) =>
    !!value && (
      <TextField key={label} source={label} label=" " />
    ))

  const thirdColumnEntries = [
    { label: "soldAbo", value: soldAbo },
    { label: "soldInfix", value: soldInfix },
    { label: "soldNormal", value: soldNormal },
    { label: "soldPoints", value: soldPoints },
    { label: "soldService", value: soldService },
  ].map(({ label, value }) => (
    !!value && (
      <TextField key={label} source={label} label=" " />
    )
  ))

  return (
    <TableContainer style={{ paddingTop: "10px", marginBottom: "10px" }}>
      <Table padding="none">
        <TableBody>
          <TableRow>
            <TableCell style={{ width: "15%" }} className={classes.noBottomBorder} />
            <TableCell style={{ width: "40%" }} className={classes.noBottomBorder}>{firstColumnEntries}</TableCell>
            <TableCell style={{ width: "15%" }} className={classes.noBottomBorder}>{secondColumnEntries}</TableCell>
            <TableCell style={{ width: "15%" }} className={classes.noBottomBorder}>{thirdColumnEntries}</TableCell>
            <TableCell style={{ width: "15%" }} className={classes.noBottomBorder} />
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const TribuneGrid = () => {
  const translate = useTranslate()

  const TribuneHeader = () => (
    <TableHead>
      <TableRow>
        <TableCell style={{ width: "15%" }} />
        <TableCell style={{ width: "40%" }}>{translate('resources.stats-overall-view.fields.block')}</TableCell>
        <TableCell style={{ width: "15%" }} />
        <TableCell style={{ width: "15%" }} />
        <TableCell style={{ width: "15%" }} />
      </TableRow>
    </TableHead>
  )

  return (
    <ArrayField source="blocks">
      <Datagrid expand={<BlockGrid />} padding="none" header={<TribuneHeader />} isRowExpandable={record => !!record.sold}>
        <TextField source="name" label="Sektor" />
        <TextField source="entered" label=" " />
        <TextField source="sold" label=" " />
        <TextField source="capacity" label=" " />
      </Datagrid>
    </ArrayField>
  )
}

const ObjectGrid = () => {
  const translate = useTranslate()

  const objectHeader = () => (
    <TableHead>
      <TableRow>
        <TableCell style={{ width: "10%" }} />
        <TableCell style={{ width: "45%" }}>{translate('resources.stats-overall-view.fields.tribune')}</TableCell>
        <TableCell style={{ width: "15%" }} />
        <TableCell style={{ width: "15%" }} />
        <TableCell style={{ width: "15%" }} />
      </TableRow>
    </TableHead>
  )

  return (
    <ArrayField source="tribunes">
      <Datagrid expand={<TribuneGrid />} padding="none" header={objectHeader}>
        <TextField source="name" label="Trybuna" />
        <TextField source="entered" label=" " />
        <TextField source="sold" label=" " />
        <TextField source="capacity" label=" " />
      </Datagrid>
    </ArrayField>
  )
}

export const OverallView = (props: ShowProps) => {
  const translate = useTranslate()

  const overallHeader = () => (
    <TableHead>
      <TableRow>
        <TableCell style={{ width: "5%" }} />
        <TableCell style={{ width: "50%" }}>{translate('resources.stats-overall-view.fields.name')}</TableCell>
        <TableCell style={{ width: "15%" }}>{translate('resources.stats-overall-view.fields.entered')}</TableCell>
        <TableCell style={{ width: "15%" }}>{translate('resources.stats-overall-view.fields.sold')}</TableCell>
        <TableCell style={{ width: "15%" }}>{translate('resources.stats-overall-view.fields.capacity')}</TableCell>
      </TableRow>
    </TableHead>
  )

  return (
    <Show title={<ViewTitle />} {...props}>
      <ArrayField source="objects">
        <Datagrid expand={<ObjectGrid />} padding="none" header={overallHeader}>
          <TextField source="name" label="Obiekt" />
          <TextField source="entered" />
          <TextField source="sold" />
          <TextField source="capacity" />
        </Datagrid>
      </ArrayField>
    </Show>
  )
}