import React, { useEffect } from 'react'
import {
  ChipFieldProps,
  DateField,
  Labeled,
  ResourceContextProvider,
  Show,
  ShowProps,
  Tab,
  TabbedShowLayout,
  TextField,
  useRecordContext,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Box, Chip, Divider, Grid, Hidden, Typography } from '@material-ui/core'
import { TicketDetails } from '../../core/ticket-details/ticket-details'
import { mapFromBookingType } from '../../core/ticket-details/ticket-details-values.mapper'
import { TicketDetailsHeader } from './TicketDetailsHeader'
import { TicketDetailsTicketsField } from './TicketDetailsTicketsField'
import { ToggleAccessButton } from './ToggleAccessButton'
import { ToggleTicketButton } from './ToggleTicketButton'
import { ResourceName } from '../../core/ResourceName'
import { LogList } from '../logs/LogList'
import { AccessList } from './accesses/AccessList'
import { AccessShow } from './accesses/AccessShow'
import { BackButton, BackButtonText } from './TicketDetailsComponents'
import { useHasAuthority } from '../hooks/useHasAuthority'
import { Authority } from '../../core/auth/Authority'

const MappedChipField = <T extends unknown>({
  mapper,
  source,
}: ChipFieldProps & { mapper: (value: T) => string }) => {
  const record = useRecordContext<TicketDetails>()
  const t = useTranslate()
  const mappedValue = mapper(record[source as string])
  return <Chip label={t(mappedValue)} />
}

const AccessDetailsView = () => {
  const { record, basePath } = useShowContext()
  const translate = useTranslate()
  const [selectedAccess, setSelectedAccess] = React.useState<
    number | undefined
  >(undefined)

  const handleAccessChange = (prop: number | undefined) => {
    setSelectedAccess(prop)
  }

  useEffect(() => {
    if (record?.accessesIds && record.accessesIds.length === 1)
      setSelectedAccess(record?.accessesIds.toString())
  }, [record, setSelectedAccess])

  return (
    <>
      <Divider />
      <Hidden xlDown={!!selectedAccess && record?.accessesIds.length === 1}>
        <Typography variant="h5">
          {translate('resources.access.actions.choose')}
        </Typography>
        <ResourceContextProvider value={ResourceName.ACCESSES}>
          <AccessList
            basePath={basePath}
            actions={false}
            filters={undefined}
            filter={{ id: record?.accessesIds.toString() }}
            title=" "
            setSelected={handleAccessChange}
          />
        </ResourceContextProvider>
      </Hidden>
      {selectedAccess && (
        <Box paddingY={2}>
          <Grid container>
            <Hidden xlDown={record?.accessesIds.length === 1}>
              <Grid item xs={1}>
                <BackButton onClick={() => setSelectedAccess(undefined)}>
                  <BackButtonText>
                    {translate('resources.access.actions.back')}
                  </BackButtonText>
                </BackButton>
              </Grid>
            </Hidden>
            <Grid item xs={record?.accessesIds.length === 1 ? 12 : 11}>
              <AccessShow
                basePath={basePath}
                actions={false}
                id={selectedAccess.toString()}
                resource={ResourceName.ACCESSES}
                title=" "
              />
            </Grid>
          </Grid>
        </Box>
      )}
    </>
  )
}

const ProfileDetailsView = () => {
  const { record } = useShowContext()
  return (
    <Tab label="resources.ticket-details.tabs.profile">
      {record?.documentType && (
        <TextField
          addLabel
          label="resources.ticket-details.fields.documentType"
          source="documentType"
        />
      )}
      {record?.pesel && (
        <TextField
          label="resources.ticket-details.fields.pesel"
          source="pesel"
        />
      )}
      {record?.documentNumber && (
        <TextField
          label="resources.ticket-details.fields.documentNumber"
          source="documentNumber"
        />
      )}
      <TextField
        label="resources.ticket-details.fields.firstname"
        source="firstname"
      />
      <TextField
        label="resources.ticket-details.fields.lastname"
        source="lastname"
      />
      {record?.gender && (
        <TextField
          label="resources.ticket-details.fields.gender"
          source="gender"
        />
      )}
      {record?.birthdate && (
        <DateField
          label="resources.ticket-details.fields.birthdate"
          source="birthdate"
        />
      )}
      {record?.company && (
        <TextField
          label="resources.ticket-details.fields.company"
          source="company"
        />
      )}
      {record?.nationality && (
        <TextField
          label="resources.ticket-details.fields.nationality"
          source="nationality"
        />
      )}
    </Tab>
  )
}

const StatusButtons = () => (
  <>
    <Divider />
    <Box p={1} display="flex" justifyContent="flex-end">
      <ToggleAccessButton />
      <ToggleTicketButton />
    </Box>
  </>
)

const TicketMainView = () => {
  const { record: ticketDetails } = useShowContext<TicketDetails>()
  return (
    <Tab label="resources.ticket-details.tabs.booking">
      <TextField
        label="resources.ticket-details.fields.eventName"
        source="eventName"
      />
      {ticketDetails?.seatId ? (
        <TextField
          label="resources.ticket-details.fields.seatId"
          source="seatId"
        />
      ) : (
        <TextField
          label="resources.ticket-details.fields.bookingId"
          source="bookingId"
        />
      )}
      {ticketDetails?.objectName && (
        <TextField
          label="resources.ticket-details.fields.blockId"
          source="objectName"
        />
      )}
      {ticketDetails?.tribuneName && (
        <TextField
          label="resources.ticket-details.fields.tribuneName"
          source="tribuneName"
        />
      )}
      {ticketDetails?.blockName && (
        <TextField
          label="resources.ticket-details.fields.blockName"
          source="blockName"
        />
      )}
      {ticketDetails?.rowNumber && (
        <TextField
          label="resources.ticket-details.fields.rowNumber"
          source="rowNumber"
        />
      )}
      <Labeled label="resources.ticket-details.fields.bookingType">
        <MappedChipField source="bookingType" mapper={mapFromBookingType} />
      </Labeled>
      {ticketDetails?.activeFrom && (
        <DateField
          label="resources.ticket-details.fields.activeFrom"
          source="activeFrom"
          showTime
        />
      )}
      {ticketDetails?.activeTo && (
        <DateField
          label="resources.ticket-details.fields.activeTo"
          source="activeTo"
          showTime
        />
      )}
      <TicketDetailsTicketsField label="resources.ticket-details.fields.tickets.name" />
      <DateField
        label="resources.ticket-details.fields.createdAt"
        source="createdAt"
        showTime
      />
      <DateField
        label="resources.ticket-details.fields.modifiedAt"
        source="modifiedAt"
        showTime
      />
      <StatusButtons />
    </Tab>
  )
}

const TicketLogsView = () => {
  const { record, basePath } = useShowContext()
  return (
    <ResourceContextProvider value={ResourceName.TICKET_LOGS}>
      <LogList
        basePath={basePath}
        filter={{ bookingId: record?.id }}
        filters={undefined}
        actions={false}
        title=" "
        noBooking
      />
    </ResourceContextProvider>
  )
}

export const TicketDetailsView = (props: ShowProps) => {
  const hasAuthority = useHasAuthority()
  return (
    <Show {...props}>
      <>
        <TicketDetailsHeader />
        <Divider />
        <TabbedShowLayout syncWithLocation={false}>
          <Tab label="resources.ticket-details.tabs.booking">
            <TicketMainView />
          </Tab>
          {hasAuthority(Authority.VIEW_LOGS) && (
            <Tab label="resources.ticket-details.tabs.logs">
              <TicketLogsView />
            </Tab>
          )}
          <Tab label="resources.ticket-details.tabs.profile">
            <ProfileDetailsView />
          </Tab>
          <Tab label="resources.ticket-details.tabs.accesses">
            <TextField
              label="resources.ticket-details.fields.variantName"
              source="variantName"
            />
            <AccessDetailsView />
          </Tab>
        </TabbedShowLayout>
      </>
    </Show>
  )
}
