import { makeStyles } from '@material-ui/core/styles'
import {
  Event,
  List,
  Person,
  Receipt,
  Sort,
  ViewComfy,
} from '@material-ui/icons'
import React, { useState } from 'react'
import { getResources, ResourceDefinition } from 'ra-core'
import {
  DashboardMenuItem,
  MenuItemLink,
  ReduxState,
  useTranslate,
} from 'react-admin'
import { useSelector } from 'react-redux'
import { SubMenu } from './AppSubmenu'
import { ResourceName } from '../../core/ResourceName'

const useStyles = makeStyles({
  open: {
    width: '240px',
  },
  closed: {
    width: '55px',
  },
})

export const AppMenu = () => {
  const sidebarOpen = useSelector(
    (state: ReduxState) => state.admin.ui.sidebarOpen,
  )
  const theme = useStyles()
  const [open, setOpen] = useState({
    menuStats: true,
  })

  const availableResources = new Set(
    useSelector<ReduxState, ResourceDefinition[]>(getResources).map(
      (r) => r.name as ResourceName,
    ),
  )
  const translate = useTranslate()

  const handleToggle = (menu: 'menuStats') =>
    setOpen((state) => ({ ...state, [menu]: !state[menu] }))

  const anyStatsResourceAvailable = (avRes: Set<ResourceName>) =>
    avRes.has(ResourceName.STATS_OVERALL_VIEW_LIST)

  return (
    <div
      style={{ userSelect: 'none' }}
      className={sidebarOpen ? theme.open : theme.closed}
    >
      <DashboardMenuItem />
      {availableResources.has(ResourceName.TICKET_INFO) && (
        <MenuItemLink
          to={`/${ResourceName.TICKET_INFO}`}
          primaryText={translate('resources.ticket-info.name')}
          leftIcon={<Receipt />}
        />
      )}
      {availableResources.has(ResourceName.CUSTOMER_INFO) && (
        <MenuItemLink
          to={`/${ResourceName.CUSTOMER_INFO}`}
          primaryText={translate('resources.customer-info.name')}
          leftIcon={<Person />}
        />
      )}
      {availableResources.has(ResourceName.LOGS) && (
        <MenuItemLink
          to={`/${ResourceName.LOGS}`}
          primaryText={translate('resources.logs.name')}
          leftIcon={<List />}
        />
      )}
      {availableResources.has(ResourceName.EVENTS) && (
        <MenuItemLink
          to={`/${ResourceName.EVENTS}`}
          primaryText={translate('resources.events.name')}
          leftIcon={<Event />}
        />
      )}
      {anyStatsResourceAvailable(availableResources) && (
        <SubMenu
          handleToggle={() => handleToggle('menuStats')}
          isOpen={open.menuStats}
          name={translate('side-menu.stats.name')}
          icon={<ViewComfy />}
        >
          {availableResources.has(ResourceName.STATS_OVERALL_VIEW_LIST) && (
            <MenuItemLink
              to={`/${ResourceName.STATS_OVERALL_VIEW_LIST}`}
              primaryText={translate('side-menu.stats.overall-view')}
              leftIcon={<Sort style={{ transform: 'scaleX(-1)' }} />}
            />
          )}
        </SubMenu>
      )}
    </div>
  )
}
