import { DataProvider, GetListResult } from "react-admin"
import { Company, EventCompany } from "./company"
import { CompanyListParams } from "./company-list.params"
import { get, getWithPagination } from "../common/fetch.utils"
import { COMPANIES_URL, EVENTS_URL } from "../api-urls"
import { CompanyDto } from "../dto/company.dto"
import { mapToCompany } from "./companies.mapper"
import { getFilterMapper } from "./company-filter.mapper"
import { mapSortCompanyParam } from "./company-sort.mapper"

const getFilterParams = (
  partialValues: Partial<Company>
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof Company)
      return mapper('c', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { event, ...filter },
      sort: { field, order },
      pagination: { page, perPage }
    }: CompanyListParams
  ): Promise<GetListResult<Company>> => {
    const attachedCompanies: number[] = []
    if (event) {
      const pendingCompanies = await get<EventCompany>(EVENTS_URL, `/GetCompanies/${event}`)
      attachedCompanies.push(...pendingCompanies.companiesIds)
    }

    const filterParams = `c=>${getFilterParams(filter)}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortCompanyParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
      range: { total }
    } = await getWithPagination<CompanyDto[]>(COMPANIES_URL, path)
    const companies = data.map((dto) => mapToCompany(dto, attachedCompanies))

    return Promise.resolve({
      data: companies,
      total
    })
  }
} as DataProvider

export default provider