import React, { useCallback, useState } from "react"
import { useNotify, useMutation, useTranslate, sanitizeFetchType, GetListResult, GET_LIST } from "react-admin"
import { useHistory } from "react-router"
import { stringify } from "querystring"
import { CardContent, CardHeader } from "@material-ui/core"
import clsx from "clsx"
import { ResourceName } from "../../core/ResourceName"
import { useCommonStyles } from "./common-styles"
import { CustomerInfoFilter } from '../../core/customer-info/customer-info.params'
import { CustomerInfo } from "../../core/customer-info/customer-info"
import SearchInput from './SearchInput'

type FindCustomerByInfoProps = Readonly<{ titleCentered?: boolean }>

export const FindCustomerByInfo = ({
  titleCentered = false
}: FindCustomerByInfoProps) => {
  const history = useHistory()
  const notify = useNotify()
  const [error, setError] = useState("")
  const [mutate, { loading }] = useMutation()
  const commonClasses = useCommonStyles()
  const t = useTranslate()

  const fetchCustomers = useCallback(
    (info: string) =>
      mutate({
        type: sanitizeFetchType(GET_LIST),
        resource: ResourceName.CUSTOMER_INFO,
        payload: { filter: { info } } as CustomerInfoFilter
      },
        { returnPromise: true },
      ) as Promise<GetListResult<CustomerInfo>>,
    [mutate]
  )

  const onValue = useCallback(
    async (info: string) => {
      if (info.replace(/\s/g, "").length === 0) {
        setError('common.find-customer-by-info.invalidQuery')
      }
      else if (info.length < 2) {
        setError('common.find-customer-by-info.tooShort')
      }
      else {
        setError('')
        try {
          const {
            total,
            data: [onlyCustomer],
          } = await fetchCustomers(info)
          if (!total) {
            setError('common.find-customer-by-info.error')
          } else if (total === 1) {
            history.push({
              pathname: `/${ResourceName.CUSTOMER_DETAILS}/${onlyCustomer.id}/show`
            })
          } else {
            history.push({
              pathname: `/${ResourceName.CUSTOMER_INFO}`,
              search: stringify({
                filter: JSON.stringify({
                  info
                } as CustomerInfoFilter['filter'])
              })
            })
          }
        } catch (err) {
          notify((err as Error)?.message, 'error')
        }
      }
    },
    [fetchCustomers, history, notify]
  )

  return (
    <>
      <CardHeader
        className={clsx({ [commonClasses.textCentered]: titleCentered })}
        title={t('common.find-customer-by-info.title')} />
      <CardContent>
        <SearchInput
          label="common.find-customer-by-info.input-label"
          onValue={onValue}
          disabled={loading}
          error={error}
        />
      </CardContent>
    </>
  )
}