import { Card, Box, Typography, Divider } from '@material-ui/core'
import React from 'react'
import { useLocation } from 'react-router'
import {
  List,
  ListProps,
  useListContext,
  useTranslate,
  Datagrid,
  TextField,
} from 'react-admin'
import { FindCustomerByInfo } from '../common/FindCustomerByInfo'
import { ResourceName } from '../../core/ResourceName'
import { MaskField } from '../common/MaskField'

const CustomerInfoGrid = () => {
  const {
    loading,
    filterValues: { info },
  } = useListContext()
  const t = useTranslate()
  return (
    <>
      <Box m={2}>
        <Typography variant="h5" paragraph>
          {t('resources.customer-info.search-list-title', { info })}
        </Typography>
      </Box>
      <Divider />
      <Datagrid
        loaded={!loading}
        rowClick={(id) => `${ResourceName.CUSTOMER_DETAILS}/${id}/show`}
      >
        <TextField source="firstname" />
        <TextField source="lastname" />
        <TextField source="pesel" />
        <MaskField source="pesel" sortable={false} />
      </Datagrid>
    </>
  )
}

export const CustomerInfoList = (props: ListProps) => {
  const { search } = useLocation()
  return (
    <>
      <Card>
        <FindCustomerByInfo titleCentered />
      </Card>
      {!!search && (
        <List
          actions={false}
          {...props}
          sort={{ field: 'lastname', order: 'ASC' }}
        >
          <CustomerInfoGrid />
        </List>
      )}
    </>
  )
}
