import { DataProvider, GetOneParams, GetOneResult } from 'react-admin'
import {
  BLOCKS_URL,
  BOOKINGS_URL,
  EVENTS_URL,
  OBJECTS_URL,
  SEATS_URL,
  TRIBUNES_URL,
} from '../../api-urls'
import { get, getByConditions } from '../../common/fetch.utils'
import { BlockDto } from '../../dto/block.dto'
import { BookingDto } from '../../dto/booking.dto'
import { EventDto } from '../../dto/event.dto'
import { ObjectDto } from '../../dto/object.dto'
import { SeatDto } from '../../dto/seat.dto'
import { TribuneDto } from '../../dto/tribune.dto'
import { OverallView } from './overall-view'
import { mapToOverallView } from './overall-view.mapper'

const provider = {
  getOne: async (
    resource: string,
    { id: eventId }: GetOneParams,
  ): Promise<GetOneResult<OverallView>> => {
    const pendingSelectedEvent = get<EventDto>(EVENTS_URL, `/${eventId}`)
    const pendingObjects = getByConditions<ObjectDto>(
      OBJECTS_URL,
      `o=>o.Events.Any(e => e.Id.Equals(${eventId}))?sortBy=o=>o.Name&sortOrder=ASC`,
    )
    const pendingTribunes = getByConditions<TribuneDto>(
      TRIBUNES_URL,
      `t=>t.Object.Events.Any(e => e.Id.Equals(${eventId}))?sortBy=t=>t.Name&sortOrder=ASC`,
    )
    const pendingBlocks = getByConditions<BlockDto>(
      BLOCKS_URL,
      `b=>b.Tribune.Object.Events.Any(e => e.Id.Equals(${eventId}))?sortBy=b=>b.Name&sortOrder=ASC`,
    )
    const pendingSeats = getByConditions<SeatDto>(
      SEATS_URL,
      `s=>s.Block.Tribune.Object.Events.Any(e => e.Id.Equals(${eventId}))`,
    )
    const bookingsCondition = `b=>b.EventId.Equals(${eventId}) && b.Disabled != TicketAPI.Models.Enums.TicketManagement.BookingDisabledOptions.STORNO`
    const pendingBookings = getByConditions<BookingDto>(
      BOOKINGS_URL,
      bookingsCondition,
    )
    const pendingEnteredBookings = getByConditions<BookingDto>(
      BOOKINGS_URL,
      `${bookingsCondition} && b.Tickets.SelectMany(t => t.AccessUsings).Any(au => au.Active == true && au.Direction.Equals(TicketAPI.Models.Enums.DeviceManagement.DeviceDirections.IN))`,
    )

    const [
      selectedEvent,
      objects,
      tribunes,
      blocks,
      seats,
      bookings,
      enteredBookings,
    ] = await Promise.all([
      pendingSelectedEvent,
      pendingObjects,
      pendingTribunes,
      pendingBlocks,
      pendingSeats,
      pendingBookings,
      pendingEnteredBookings,
    ])

    const overallViewData = mapToOverallView(
      selectedEvent,
      objects,
      tribunes,
      blocks,
      seats,
      bookings,
      enteredBookings,
    )

    return {
      data: overallViewData,
    }
  },
} as DataProvider

export default provider
