import {
  List,
  ListProps,
  useListContext,
  useTranslate,
  useMutation,
  sanitizeFetchType,
  Record,
  useRefresh,
  UPDATE_MANY,
  useListSortContext,
  TextField,
  DateField,
  FunctionField,
} from 'react-admin'
import React from 'react'
import {
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core'
import { ArrowDownward, ArrowUpward } from '@material-ui/icons'
import { AccessUsingDto } from '../../../core/dto/access-using.dto'
import { ResourceName } from '../../../core/ResourceName'

const AccessUsingsGrid = () => {
  const translate = useTranslate()
  const { data, ids } = useListContext()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const { currentSort, setSort } = useListSortContext()

  const patchAccess = async (access: AccessUsingDto) => {
    await mutate(
      {
        type: sanitizeFetchType(UPDATE_MANY),
        resource: ResourceName.ACCESS_USINGS,
        payload: {
          ids: [access.id],
          data: {
            active: !access.active,
          },
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const inverseOrder = () => (currentSort.order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (field: string) => {
    setSort(field, currentSort.field === field ? inverseOrder() : 'DESC')
  }

  const HeaderCell = (name: string, display: string) => (
    <div style={{ display: 'flex', cursor: 'pointer', textAlign: 'center' }}>
      {currentSort.field === name && (
        <>{currentSort.order === 'ASC' ? <ArrowUpward /> : <ArrowDownward />}</>
      )}
      <div> {translate(display)} </div>
    </div>
  )

  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell onClick={() => handleSortChange('date')}>
              {HeaderCell('date', 'resources.access-usings.fields.date')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('direction')}>
              {HeaderCell('direction', 'resources.access-usings.fields.direction')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('active')}>
              {HeaderCell('active', 'resources.access-usings.fields.active')}
            </TableCell>
            <TableCell>
              {translate('resources.access-usings.fields.ticketId')}
            </TableCell>
            <TableCell style={{ width: '20%' }} />
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <TableRow key={id}>
              <TableCell>
                <DateField
                  source="date"
                  record={data[id] as AccessUsingDto}
                  showTime
                />
              </TableCell>
              <TableCell>
                <TextField
                  source="direction"
                  record={data[id] as AccessUsingDto}
                />
              </TableCell>
              <TableCell>
                <FunctionField
                  record={data[id]}
                  render={(record: Record | undefined) =>
                    record?.active
                      ? translate('const.yes')
                      : translate('const.no')
                  }
                />
              </TableCell>
              <TableCell>
                <TextField
                  source="ticketCode"
                  record={data[id] as AccessUsingDto}
                />
              </TableCell>
              <TableCell>
                <Button
                  variant="contained"
                  color="secondary"
                  style={{ width: '100%', fontSize: '13px' }}
                  onClick={() => patchAccess(data[id] as AccessUsingDto)}
                >
                  {translate(
                    `resources.access-usings.actions.${data[id].active ? `deactivate` : `activate`
                    }`,
                  )}
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const AccessUsingsList = (props: ListProps) => (
  <List
    {...props}
    exporter={false}
    perPage={20}
    sort={{ field: 'date', order: 'DESC' }}
  >
    <AccessUsingsGrid />
  </List>
)
