import { usePermissions } from 'react-admin'
import { Authority } from '../../core/auth/Authority'
import { mapAuthorityToRequiredPermissions } from '../../core/auth/authority.mapper'

export const hasAuthority = (authority: Authority, permissions: Set<string>) => {
  const permissionSet = new Set(permissions)
  const requiredPerms = mapAuthorityToRequiredPermissions(authority)
  return requiredPerms.every(reqPerms => permissionSet.has(reqPerms))
}

export const useHasAuthority = () => {
  const { permissions, loaded } = usePermissions<Set<string>>()
  return (authority: Authority) => {
    if (loaded && !permissions?.size) {
      // eslint-disable-next-line no-console
      console.error('"useHasAuthority" - No permissions or hook was used outside the react admin context')
      return false
    }
    return hasAuthority(authority, permissions as Set<string>)
  }
}