import { DataProvider, GetListResult } from "react-admin"
import { EventObject, ObjectType } from "./object"
import { ObjectListParams } from "./object-list.params"
import { get, getWithPagination } from "../common/fetch.utils"
import { OBJECTS_URL, EVENTS_URL } from "../api-urls"
import { ObjectDto } from "../dto/object.dto"
import { mapToObject } from "./object.mapper"
import { getFilterMapper } from "./object-filter.mapper"
import { mapSortCompanyParam } from "./object-sort.mapper"

const getFilterParams = (
  partialValues: Partial<ObjectType>
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof ObjectType)
      return mapper('o', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { event, ...filter },
      sort: { field, order },
      pagination: { page, perPage }
    }: ObjectListParams
  ): Promise<GetListResult<ObjectType>> => {
    const attachedObjects: number[] = []
    if (event) {
      const pendingEvent = get<EventObject>(EVENTS_URL, `/${event}`)
      const { objectsIds } = await Promise.resolve(pendingEvent)
      attachedObjects.push(...objectsIds)
    }

    const filterParams = `o=>${getFilterParams(filter)}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortCompanyParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
      range: { total }
    } = await getWithPagination<ObjectDto[]>(`${OBJECTS_URL}/GetByConditions`, path)
    const companies = data.map((dto) => mapToObject(dto, attachedObjects))

    return Promise.resolve({
      data: companies,
      total
    })
  }
} as DataProvider

export default provider