import {
  DataProvider, GetListResult
} from 'react-admin'
import { AccessDateDto } from '../dto/access-date.dto'
import { getFilterMapper } from './access-dates-filter.mapper'
import { AccessDatesListParams } from './access-dates-list.params'
import { mapSortAccessDatesParam } from './access-dates-sort.mapper'
import { getWithPagination } from '../common/fetch.utils'
import { ACCESS_DATES } from '../api-urls'

const getFilterParams = (filter: Partial<AccessDateDto>) => {
  if (Object.keys(filter).length) {
    const filterParams = Object.entries(filter).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof AccessDateDto)
      return mapper("ad", value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return "true"
}

const provider = {
  getList: async (
    resource: string,
    {
      filter,
      sort: { field, order },
      pagination: { page, perPage }
    }: AccessDatesListParams
  ): Promise<GetListResult<AccessDateDto>> => {
    const filterParams = `ad=>${getFilterParams(filter).toString()}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortAccessDatesParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
      range: { total }
    } = await getWithPagination<AccessDateDto[]>(`${ACCESS_DATES}/GetByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  }
} as DataProvider

export default provider