import {
  List,
  ListProps,
  useListContext,
  useTranslate,
  TextField,
  DateField,
  useListSortContext
} from 'react-admin'
import React, { useState } from 'react'
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Collapse,
} from '@material-ui/core'
import { ArrowDropUp, ArrowDropDown, ArrowUpward, ArrowDownward } from '@material-ui/icons'
import { AccessDateDto } from '../../../core/dto/access-date.dto'

const AccessDatesRow = ({ record }: { record: AccessDateDto }) => {
  const [open, setOpen] = useState(false)
  const translate = useTranslate()
  return (
    <>
      <TableRow>
        <TableCell padding="checkbox">
          <IconButton
            onClick={() => setOpen((state) => !state)}
            style={{ width: 40, height: 40 }}
          >
            {open ? <ArrowDropUp /> : <ArrowDropDown />}
          </IconButton>
        </TableCell>
        <TableCell>
          <DateField record={record} source="activeFrom" />
        </TableCell>
        <TableCell>
          <DateField record={record} source="activeTo" />
        </TableCell>
        <TableCell>
          <TextField record={record} source="type" />
        </TableCell>
        <TableCell>
          <TextField record={record} source="priority" />
        </TableCell>
      </TableRow>
      {(record.dayOfWeek || record.timeFrom || record.timeTo) && (
        <TableRow>
          <TableCell colSpan={5} style={{ paddingBottom: 0, paddingTop: 0 }}>
            <Collapse in={open}>
              <TableContainer>
                <Table>
                  <TableBody>
                    {record.dayOfWeek && (
                      <TableRow>
                        <TableCell>
                          {translate('resources.access-dates.fields.dayOfWeek')}
                        </TableCell>
                        <TableCell style={{ textAlign: 'end' }}>
                          <TextField
                            record={record}
                            source="dayOfWeek"
                            label=" "
                          />
                        </TableCell>
                      </TableRow>
                    )}
                    {record.timeFrom && (
                      <TableRow>
                        <TableCell>
                          {translate('resources.access-dates.fields.timeFrom')}
                        </TableCell>
                        <TableCell style={{ textAlign: 'end' }}>
                          <DateField
                            record={record}
                            source="timeFrom"
                            label=" "
                            showTime
                          />
                        </TableCell>
                      </TableRow>
                    )}
                    {record.timeTo && (
                      <TableRow>
                        <TableCell>
                          {translate('resources.access-dates.fields.timeTo')}
                        </TableCell>
                        <TableCell style={{ textAlign: 'end' }}>
                          <DateField
                            record={record}
                            source="timeTo"
                            label=" "
                            showTime
                          />
                        </TableCell>
                      </TableRow>
                    )}
                  </TableBody>
                </Table>
              </TableContainer>
            </Collapse>
          </TableCell>
        </TableRow>
      )}
    </>
  )
}

const AccessDatesGrid = () => {
  const translate = useTranslate()
  const { data, ids } = useListContext()
  const { currentSort, setSort } = useListSortContext()

  const inverseOrder = () => (currentSort.order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (field: string) => {
    setSort(field, currentSort.field === field ? inverseOrder() : 'DESC')
  }

  const HeaderCell = (name: string, display: string) => (
    <div style={{ display: 'flex', cursor: 'pointer', textAlign: 'center' }}>
      {currentSort.field === name && (
        <>{currentSort.order === 'ASC' ? <ArrowUpward /> : <ArrowDownward />}</>
      )}
      <div> {translate(display)} </div>
    </div>
  )

  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell style={{ width: '5%' }} />
            <TableCell onClick={() => handleSortChange('activeFrom')}>
              {HeaderCell('activeFrom', 'resources.access-dates.fields.activeFrom')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('activeTo')}>
              {HeaderCell('activeTo', 'resources.access-dates.fields.activeTo')}
            </TableCell>
            <TableCell onClick={() => handleSortChange('type')}>
              {HeaderCell('type', 'resources.access-dates.fields.type')}
            </TableCell>
            <TableCell>
              {translate('resources.access-dates.fields.priority')}
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <AccessDatesRow key={id} record={data[id] as AccessDateDto} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const AccessDatesList = (props: ListProps) => (
  <List
    {...props}
    exporter={false}
    perPage={20}
    sort={{ field: 'id', order: 'DESC' }}
  >
    <AccessDatesGrid />
  </List>
)
