import React from 'react'
import {
  useMediaQuery,
  useTheme,
  TableContainer,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
} from '@material-ui/core'
import { ListProps, List, Pagination, useListContext, useTranslate } from 'react-admin'
import { MobilePostPagination } from '../../common/Pagination'

interface AccessListProps extends ListProps {
  setSelected: (prop: number | undefined) => void
}

const AccessGrid = ({
  setSelected,
}: {
  setSelected: (prop: number | undefined) => void
}) => {
  const translate = useTranslate()
  const { data, ids } = useListContext()

  return (
    <>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>{translate('resources.access.fields.name')}</TableCell>
              <TableCell>{translate('resources.access.fields.entranceId')}</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {ids.map((id) => (
              <TableRow key={id} style={{ cursor: 'pointer' }} hover>
                <TableCell onClick={() => setSelected(id as number)}>
                  {data[id]?.id}
                </TableCell>
                <TableCell onClick={() => setSelected(id as number)}>
                  {data[id]?.name}
                </TableCell>
                <TableCell onClick={() => setSelected(id as number)}>
                  {data[id]?.entranceId}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  )
}

export const AccessList = ({setSelected, ...props}: AccessListProps) => {
  const theme = useTheme()

  return (
    <List
      perPage={20}
      exporter={false}
      pagination={
        useMediaQuery(theme.breakpoints.up('md')) ? (
          <Pagination rowsPerPageOptions={[20, 50, 100]} />
        ) : (
          <MobilePostPagination />
        )
      }
      {...props}
    >
      <AccessGrid setSelected={setSelected} />
    </List>
  )
}
