import { BlockDto } from '../../dto/block.dto'
import { BookingDto } from '../../dto/booking.dto'
import { EventDto } from '../../dto/event.dto'
import { ObjectDto } from '../../dto/object.dto'
import { SeatDto } from '../../dto/seat.dto'
import { TribuneDto } from '../../dto/tribune.dto'
import { BookingType } from '../../enum/BookingType'
import {
  BlockOverallView,
  ObjectOverallView,
  OverallView,
  TribuneOverallView,
} from './overall-view'

const calculateCapacity = (seats: SeatDto[], bookings: BookingDto[]) =>
  seats.length + bookings.filter((b) => b.seatId == null).length

const mapToBlockOverallView = (
  block: BlockDto,
  seats: SeatDto[],
  bookings: BookingDto[],
  enteredBookings: BookingDto[],
): BlockOverallView => ({
  id: block.id,
  name: block.name,
  capacity: calculateCapacity(seats, bookings),
  sold: bookings.length,
  entered: enteredBookings.length,
  soldNormal: bookings.filter((b) => b.type === BookingType.NORMAL).length,
  enteredNormal: enteredBookings.filter((b) => b.type === BookingType.NORMAL)
    .length,
  soldAbo: bookings.filter((b) => b.type === BookingType.ABO).length,
  enteredAbo: enteredBookings.filter((b) => b.type === BookingType.ABO).length,
  soldInfix: bookings.filter((b) => b.type === BookingType.INFIX).length,
  enteredInfix: enteredBookings.filter((b) => b.type === BookingType.INFIX)
    .length,
  soldService: bookings.filter((b) => b.type === BookingType.SERVICE).length,
  enteredService: enteredBookings.filter((b) => b.type === BookingType.SERVICE)
    .length,
  soldPoints: bookings.filter((b) => b.type === BookingType.POINTS).length,
  enteredPoints: enteredBookings.filter((b) => b.type === BookingType.POINTS)
    .length,
})

const mapToTribuneOverallView = (
  tribune: TribuneDto,
  blocks: BlockDto[],
  seats: SeatDto[],
  bookings: BookingDto[],
  enteredBookingIds: number[],
): TribuneOverallView => ({
  id: tribune.id,
  name: tribune.name,
  capacity: calculateCapacity(seats, bookings),
  sold: bookings.length,
  entered: enteredBookingIds.length,
  blocks: blocks
    .filter((b) => tribune.blocksIds.includes(b.id))
    .map((block) => {
      const filteredSeats = seats.filter((s) => s.blockId === block.id)
      const filteredBookingIds = block.bookingsIds.concat(
        filteredSeats.flatMap((s) => s.bookingsIds),
      )
      const filteredBookings = bookings.filter((book) =>
        filteredBookingIds.includes(book.id),
      )
      const filteredEnteredBookingIds = enteredBookingIds.filter(
        (enteredBookingId) => filteredBookingIds.includes(enteredBookingId),
      )
      const filteredEnteredBookings = bookings.filter((book) =>
        filteredEnteredBookingIds.includes(book.id),
      )
      return mapToBlockOverallView(
        block,
        filteredSeats,
        filteredBookings,
        filteredEnteredBookings,
      )
    }),
})

const mapToObjectOverallView = (
  object: ObjectDto,
  tribunes: TribuneDto[],
  blocks: BlockDto[],
  seats: SeatDto[],
  bookings: BookingDto[],
  enteredBookingIds: number[],
): ObjectOverallView => ({
  id: object.id,
  name: object.name,
  capacity: calculateCapacity(seats, bookings),
  sold: bookings.length,
  entered: enteredBookingIds.length,
  tribunes: tribunes
    .filter((t) => object.tribunesIds.includes(t.id))
    .map((tribune) => {
      const filteredBlocks = blocks.filter((b) => b.tribuneId === tribune.id)
      const filteredSeatIds = filteredBlocks.flatMap((b) => b.seatsIds)
      const filteredSeats = seats.filter((s) => filteredSeatIds.includes(s.id))
      const filteredBookingIds = filteredBlocks
        .flatMap((b) => b.bookingsIds)
        .concat(filteredSeats.flatMap((s) => s.bookingsIds))
      const filteredBookings = bookings.filter((book) =>
        filteredBookingIds.includes(book.id),
      )
      const filteredEnteredBookingIds = enteredBookingIds.filter(
        (enteredBookingId) => filteredBookingIds.includes(enteredBookingId),
      )
      return mapToTribuneOverallView(
        tribune,
        filteredBlocks,
        filteredSeats,
        filteredBookings,
        filteredEnteredBookingIds,
      )
    }),
})

export const mapToOverallView = (
  selectedEvent: EventDto,
  objects: ObjectDto[],
  tribunes: TribuneDto[],
  blocks: BlockDto[],
  seats: SeatDto[],
  bookings: BookingDto[],
  enteredBookings: BookingDto[],
): OverallView => {
  const enteredBookingIds = enteredBookings.map((book) => book.id)
  return {
    id: selectedEvent.id,
    eventName: selectedEvent.name,
    capacity: calculateCapacity(seats, bookings),
    sold: bookings.length,
    entered: enteredBookings.length,
    date: selectedEvent.dateStart,
    objects: objects
      .filter((o) => selectedEvent.objectsIds.includes(o.id))
      .map((object) => {
        const filteredTribunes = tribunes.filter(
          (t) => t.objectId === object.id,
        )
        const filteredBlocks = blocks.filter((b) =>
          filteredTribunes.flatMap((t) => t.blocksIds).includes(b.id),
        )
        const filteredSeatIds = filteredBlocks.flatMap((b) => b.seatsIds)
        const filteredSeats = seats.filter((s) =>
          filteredSeatIds.includes(s.id),
        )
        const filteredBookingIds = filteredBlocks
          .flatMap((b) => b.bookingsIds)
          .concat(filteredSeats.flatMap((s) => s.bookingsIds))
        const filteredBookings = bookings.filter((book) =>
          filteredBookingIds.includes(book.id),
        )
        const filteredEnteredBookingIds = enteredBookingIds.filter(
          (enteredBookingId) => filteredBookingIds.includes(enteredBookingId),
        )
        return mapToObjectOverallView(
          object,
          filteredTribunes,
          filteredBlocks,
          filteredSeats,
          filteredBookings,
          filteredEnteredBookingIds,
        )
      }),
  }
}
