import { DataProvider, GetListResult } from "react-admin"
import { EngineDto } from "../dto/engine.dto"
import { Engine, EventEngine } from "./engine"
import { EngineListParams } from "./engine-list.params"
import { get, getWithPagination } from "../common/fetch.utils"
import { ENGINES_URL, EVENTS_URL } from "../api-urls"
import { getFilterMapper } from "./engine-filter.mapper"
import { mapSortEngineParam } from "./engine-sort.mapper"
import { mapToEvents } from "./engine.mapper"

const getFilterParams = (
  partialValues: Partial<Engine>
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof Engine)
      return mapper('o', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { event, ...filter },
      sort: { field, order },
      pagination: { page, perPage }
    }: EngineListParams
  ): Promise<GetListResult<Engine>> => {
    const attachedDevices: number[] = []
    if (event) {
      const pendingDevices = await get<EventEngine>(EVENTS_URL, `/GetDevices/${event}`)
      attachedDevices.push(...pendingDevices.verifierDevicesIds)
    }

    const filterParams = `o=>${getFilterParams(filter)}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortEngineParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage - 1}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
      range: { total }
    } = await getWithPagination<EngineDto[]>(`${ENGINES_URL}/GetByConditions`, path)
    const companies = data.map((dto) => mapToEvents(dto, attachedDevices))

    return Promise.resolve({
      data: companies,
      total
    })
  }
} as DataProvider

export default provider