import { BookingDto } from "../dto/booking.dto";
import { EventDto } from "../dto/event.dto";
import { VariantDto } from "../dto/variant.dto";
import { Booking } from "./booking";

export const mapToBookings = (
  {
    id,
    type,
    disabled,
    activeFrom,
    activeTo,
    eventId,
    seatId,
    blockId,
    customerId,
    variantId,
    ticketsIds,
    createdAt,
    modifiedAt
  }: BookingDto,
  events: EventDto[],
  variants: VariantDto[]
): Booking => {
  const foundEvent = events.find((event) => event.id === eventId)
  const foundVariant = variants.find((variant) => variant.id === variantId)
  return({
    id,
    type,
    disabled,
    activeFrom,
    activeTo,
    eventId,
    seatId,
    blockId,
    customerId,
    variantId,
    ticketsIds,
    createdAt,
    modifiedAt,
    eventName: foundEvent?.name,
    eventDateStart: foundEvent?.dateStart,
    variantName: foundVariant?.name
  })
}