import { AuthProvider } from 'react-admin'
import { authPost, getWithToken } from '../common/fetch.utils'
import { AuthDto } from '../dto/auth.dto'
import { AUTH_URL, PERMISSIONS_URL } from '../api-urls'
import { AuthRequest } from './auth.request'
import { PermissionDto } from '../dto/permission.dto'

const authProvider: AuthProvider = {
  login: async ({ username, password }) => {
    const { token } = await authPost<AuthRequest, AuthDto>(AUTH_URL, {
      login: username,
      password,
    })
    const permissionDtos = await getWithToken<PermissionDto[]>(
      PERMISSIONS_URL,
      token,
    )
    const permissions = permissionDtos.map((p) => p.name)
    localStorage.setItem('username', username)
    localStorage.setItem('token', token)
    localStorage.setItem('permissions', permissions.join())
  },
  logout: () => {
    localStorage.removeItem('username')
    localStorage.removeItem('token')
    localStorage.removeItem('permissions')
    return Promise.resolve()
  },
  checkError: ({ status }) => {
    if (status === 401 || status === 403) {
      localStorage.removeItem('username')
      localStorage.removeItem('token')
      localStorage.removeItem('permissions')
      return Promise.reject()
    }
    return Promise.resolve()
  },
  checkAuth: () =>
    localStorage.getItem('token') ? Promise.resolve() : Promise.reject(),
  getPermissions: () => {
    const permissions = localStorage.getItem('permissions') as string
    return Promise.resolve(new Set(permissions?.split(',') || []))
  },
  getIdentity: () =>
    Promise.resolve({
      id: localStorage.getItem('username') as string,
    }),
}

export default authProvider
