import {
  Button,
  ButtonGroup,
  Card,
  CardContent,
  createStyles,
  FormControlLabel,
  Grid,
  makeStyles,
  MenuItem,
  Paper,
  Select,
  Switch,
  Theme,
  Typography,
} from '@material-ui/core'
import React, { Dispatch, ReactElement } from 'react'
import { Title, useLocale, useSetLocale, useTranslate } from 'react-admin'
import { useDispatch, useSelector } from 'react-redux'
import { Event, List, Search } from '@material-ui/icons'
import { ThemeState } from '../../core/common/theme.reducer'
import {
  CHANGE_THEME,
  UPDATE_DASHBOARD,
} from '../../core/constants/reducer.actions'
import {
  DashboardAction,
  DashboardItem,
  DashboardState,
} from '../../core/common/dashboard'
import { useHasAuthority } from '../hooks/useHasAuthority'
import { Authority } from '../../core/auth/Authority'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    label: { width: '15vw', display: 'inline-block' },
    button: { margin: theme.spacing(2) },
    select: { width: '40vw', margin: theme.spacing(2) },
    compsContainer: {
      width: '40vw',
      display: 'inline-block',
      margin: theme.spacing(2),
    },
  }),
)

type DashboardItemSwitchProps = {
  dashboardItem: DashboardItem
  label: string
  icon: ReactElement
}

const DashboardItemSwitch = ({
  dashboardItem,
  label,
  icon,
}: DashboardItemSwitchProps) => {
  const t = useTranslate()
  const dispatch = useDispatch<Dispatch<DashboardAction>>()
  const inactiveComponents = useSelector(
    (state: DashboardState) => state.dashboard.inactiveComponents,
  )
  const onSwitch = ({
    target: { name },
  }: React.ChangeEvent<HTMLInputElement>) => {
    dispatch({ type: UPDATE_DASHBOARD, payload: name as DashboardItem })
  }
  return (
    <Grid item xs={6}>
      <Paper>
        {icon}
        <FormControlLabel
          control={
            <Switch
              checked={!inactiveComponents.includes(dashboardItem)}
              name={dashboardItem}
              onChange={onSwitch}
            />
          }
          label={t(label)}
          labelPlacement="top"
          style={{ margin: '5%', textAlign: 'center', width: '90%' }}
        />
      </Paper>
    </Grid>
  )
}

const DashboardCustomization = () => {
  const hasAuthority = useHasAuthority()
  return (
    <Grid container spacing={1}>
      <DashboardItemSwitch
        dashboardItem={DashboardItem.TICKET_FINDER}
        label="dashboard.settings.ticket-finder-label"
        icon={<Search />}
      />
      {hasAuthority(Authority.VIEW_LOGS) && (
        <DashboardItemSwitch
          dashboardItem={DashboardItem.LAST_LOGS}
          label="dashboard.settings.last-logs-label"
          icon={<List />}
        />
      )}
      <DashboardItemSwitch
        dashboardItem={DashboardItem.LAST_EVENTS}
        label="dashboard.settings.last-events-label"
        icon={<Event />}
      />
    </Grid>
  )
}

export const Configuration = () => {
  const dispatch = useDispatch()
  const theme = useSelector(
    (state: ThemeState) => state.theme.darkMode === 'dark',
  )
  const classes = useStyles()
  const locale = useLocale()
  const setLocale = useSetLocale()
  const t = useTranslate()

  return (
    <Card>
      <Title title={t('settings.name')} />
      <CardContent>
        <Typography className={classes.label}>
          {t('settings.darkMode.name')}
        </Typography>
        <ButtonGroup className={classes.button}>
          <Button
            size="large"
            variant="contained"
            color={!theme ? 'primary' : 'default'}
            onClick={() => theme && dispatch({ type: CHANGE_THEME })}
          >
            {t('settings.darkMode.light')}
          </Button>
          <Button
            size="large"
            variant="contained"
            color={theme ? 'primary' : 'default'}
            onClick={() => !theme && dispatch({ type: CHANGE_THEME })}
          >
            {t('settings.darkMode.dark')}
          </Button>
        </ButtonGroup>
      </CardContent>
      <CardContent>
        <Typography className={classes.label}>
          {t('settings.language.name')}
        </Typography>
        <Select
          value={locale}
          onChange={(e) => setLocale(e.target.value as string)}
          className={classes.select}
          variant="outlined"
        >
          <MenuItem value="pl">{t('settings.language.pl')}</MenuItem>
          <MenuItem value="en">{t('settings.language.en')}</MenuItem>
        </Select>
      </CardContent>
      <CardContent>
        <Typography className={classes.label}>
          {t('dashboard.settings.title')}
        </Typography>
        <div className={classes.compsContainer}>
          <DashboardCustomization />
        </div>
      </CardContent>
    </Card>
  )
}
