import { CustomerInfo } from './customer-info'

const mapping: Partial<Record<keyof CustomerInfo, string>> = {
  id: 'c=>c.Id',
  firstname: 'c=>c.Firstname',
  lastname: 'c=>c.Lastname',
  pesel: 'c=>c.Pesel',
  documentNumber: 'c=>c.DocumentNumber'
}

export const mapSortCustomerParams = (key: keyof CustomerInfo) => mapping[key]