import { Access } from './access'

const mapping: Partial<
  Record<
    keyof Access,
    (lambdaParameter: string, value: string | number) => string
  >
> = {
  id: (lambdaParameter, value) => value.toString().split(",").map((v) => `${v}==${lambdaParameter}.Id`).join("&&"),
}

export const getFilterMapper = (key: keyof Access) =>
  mapping[key] as (lambdaParameter: string, value: string | number) => string
