import { DataProvider, GetListResult } from "react-admin"
import { Palm, EventPalm } from "./palm"
import { PalmListParams } from "./palm-list.params"
import { get, getWithPagination } from "../common/fetch.utils"
import { EVENTS_URL, PALMS_URL } from "../api-urls"
import { getFilterMapper } from "./palm-filter.mapper"
import { mapSortPalmParam } from "./palm-sort.mapper"
import { mapToPalms } from "./palm.mapper"
import { PalmDto } from "../dto/palm.dto"

const getFilterParams = (
  partialValues: Partial<Palm>
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof Palm)
      return mapper('o', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { event, ...filter },
      sort: { field, order },
      pagination: { page, perPage }
    }: PalmListParams
  ): Promise<GetListResult<Palm>> => {
    const attachedDevices: number[] = []
    if (event) {
      const pendingDevices = await get<EventPalm>(EVENTS_URL, `/GetDevices/${event}`)
      attachedDevices.push(...pendingDevices.verifierDevicesIds)
    }

    const filterParams = `o=>${getFilterParams(filter)}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortPalmParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage - 1}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
      range: { total }
    } = await getWithPagination<PalmDto[]>(`${PALMS_URL}/GetByConditions`, path)
    const companies = data.map((dto) => mapToPalms(dto, attachedDevices))

    return Promise.resolve({
      data: companies,
      total
    })
  }
} as DataProvider

export default provider