import {
  Edit, EditProps, TextField, TextInput, FunctionField,
  DateTimeInput, useTranslate, SimpleForm, Record
} from "react-admin"
import React, { useMemo } from "react"

const EditTitle = (record: any) => {
  const { record: { name, dateStart, id } } = record
  const translate = useTranslate()
  const date = useMemo(() => !(dateStart instanceof Date) ? dateStart.replace('T', ' ') : '', [dateStart])
  return (
    <span>{translate('resources.events.titles.edit')}: {name} {date} {id}</span>
  )
}

export const EventEdit = (props: EditProps) => {
  const translate = useTranslate()
  return (
    <Edit {...props} title={<EditTitle />}>
      <SimpleForm>
        <TextField source="id" label="ID" />
        <TextInput source="name" />
        <DateTimeInput source="dateStart" />
        <DateTimeInput source="dateEnd" />
        <FunctionField resource="events" label={translate("resources.events.fields.status")}
          render={(r: Record | undefined) =>
            (translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))} />
      </SimpleForm>
    </Edit>
  )
}