import { AccessDateDto } from '../dto/access-date.dto'

const mapping: Partial<
  Record<keyof AccessDateDto, (lambdaParameter: string, value: string | number) => string>
> = {
  id: (lambdaParameter, value) => value.toString().split(",").map((v) => `${v}==${lambdaParameter}.Id`).join("&&"),
}

export const getFilterMapper = (key: keyof AccessDateDto) =>
  mapping[key] as (lambdaParameter: string, value: string | number) => string
