import React, { useState } from 'react'
import {
  ShowProps,
  Show,
  useShowContext,
  ResourceContextProvider,
  useTranslate,
  TextField,
  DateField,
  FunctionField,
  Record,
  Pagination
} from 'react-admin'
import {
  TableContainer,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Grid,
  Paper,
  Box,
} from '@material-ui/core'
import { Access } from '../../../core/accesses/access'
import { FullWidthButton } from '../TicketDetailsComponents'
import { ResourceName } from '../../../core/ResourceName'
import { AccessUsingsList } from './AccessUsingsList'
import { AccessDatesList } from './AccessDatesList'
import { AccessActivePeriodStartingOptions } from '../../../core/enum/AccessActivePeriodStartingOptions'

const TabButton = ({
  value,
  selected,
  change,
  children,
}: {
  value: number
  selected: number
  change: (prop: number) => void
  children: any
}) => (
  <FullWidthButton
    style={{
      background: selected === value ? 'rgba(0,0,0,0.2)' : 'inherit',
    }}
    onClick={() => change(value)}
  >
    {children}
  </FullWidthButton>
)

