import { DataProvider, GetOneParams, GetOneResult } from "react-admin"
import { CustomerDetails } from "./customer-details"
import { CustomerDto } from "../dto/customer.dto"
import { CUSTOMERS_URL } from "../api-urls"
import { get } from '../common/fetch.utils'

const provider = {
  getOne: async (
    resource: string,
    { id: customerId }: GetOneParams,
  ): Promise<GetOneResult<CustomerDetails>> => {
    const customer = await get<CustomerDto>(CUSTOMERS_URL, `/${customerId}`)
    return {
      data: customer as CustomerDetails
    }
  }
} as DataProvider

export default provider