import {
  DateField,
  FunctionField,
  Record,
  ResourceContextProvider,
  Show,
  ShowProps,
  Tab,
  TabbedShowLayout,
  TextField,
  useShowContext,
  useTranslate,
} from 'react-admin'
import React, { useMemo } from 'react'
import { Box } from '@material-ui/core'
import { LogList } from '../logs/LogList'
import { ResourceName } from '../../core/ResourceName'
import { BookingList } from './CustomerBookingsList'
import { useHasAuthority } from '../hooks/useHasAuthority'
import { Authority } from '../../core/auth/Authority'

const CustomerBookingsView = () => {
  const { record, basePath } = useShowContext()
  return (
    <Box paddingY={2}>
      <ResourceContextProvider value={ResourceName.BOOKINGS}>
        <BookingList
          basePath={basePath}
          filter={{ id: record?.bookingsIds.join(',') }}
          filters={undefined}
          actions={false}
          title=" "
        />
      </ResourceContextProvider>
    </Box>
  )
}

const CustomerLogsView = () => {
  const { record, basePath } = useShowContext()
  return (
    <Box paddingY={2}>
      <ResourceContextProvider value={ResourceName.TICKET_LOGS}>
        <LogList
          basePath={basePath}
          filter={{ bookingId: record?.bookingsIds.join(',') }}
          filters={undefined}
          actions={false}
          title=" "
        />
      </ResourceContextProvider>
    </Box>
  )
}

const CustomerTabs = () => {
  const { record } = useShowContext()
  const translate = useTranslate()
  const hasAuthority = useHasAuthority()
  const bookingPresent = useMemo(() => record?.bookingsIds.length > 0, [record])
  return (
    <TabbedShowLayout>
      <Tab label={translate('resources.customer-details.tabs.profile')}>
        <TextField
          source="firstname"
          label="resources.customer-details.fields.firstname"
        />
        <TextField
          source="lastname"
          label="resources.customer-details.fields.lastname"
        />
        {record?.company && (
          <TextField
            source="company"
            label="resources.customer-details.fields.company"
          />
        )}
        {record?.birthdate && (
          <>
            {record.birthdate.split('.')[0].length === 1 && 0}
            <DateField
              source="birthdate"
              label="resources.customer-details.fields.birthdate"
            />
          </>
        )}
        {record?.documentType && (
          <TextField
            source="documentType"
            label="resources.customer-details.fields.documentType"
          />
        )}
        {record?.pesel ? (
          <TextField
            source="pesel"
            label="resources.customer-details.fields.pesel"
          />
        ) : (
          <TextField
            source="documentNumber"
            label="resources.customer-details.fields.documentNumber"
          />
        )}
        {record?.nationality && (
          <TextField
            source="nationality"
            label="resources.customer-details.fields.nationality"
          />
        )}
        {record?.gender && (
          <FunctionField
            label="resources.customer-details.fields.gender"
            render={(r: Record | undefined) =>
              r && translate(`resources.enums.gender.${r.gender}`)
            }
          />
        )}
      </Tab>
      {bookingPresent && (
        <Tab label={translate('resources.customer-details.tabs.bookings')}>
          <CustomerBookingsView />
        </Tab>
      )}
      {bookingPresent && hasAuthority(Authority.VIEW_LOGS) && (
        <Tab label={translate('resources.customer-details.tabs.logs')}>
          <CustomerLogsView />
        </Tab>
      )}
    </TabbedShowLayout>
  )
}

export const CustomerDetailsView = (props: ShowProps) => (
  <Show {...props}>
    <CustomerTabs />
  </Show>
)
