import * as React from 'react'
import {
  Datagrid,
  DateField,
  Filter,
  FilterProps,
  List,
  ListProps,
  TextField,
  TextInput,
  useListContext,
} from 'react-admin'
import { ResourceName } from '../../../core/ResourceName'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import { Authority } from '../../../core/auth/Authority'

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)

const OverallViewGrid = () => {
  const { loading } = useListContext()
  const hasAuthority = useHasAuthority()
  // TODO: header doesn't change color when theme value is switched
  //  the reason is the need to change the theme at <Admin> level - not at <Layout> level
  //  the solution is here - https://stackoverflow.com/a/63967145 -> after that customReducer needs to be removed
  return (
    <Datagrid
      loaded={!loading}
      rowClick={
        hasAuthority(Authority.VIEW_STATS_OVERALL_VIEW)
          ? (id) => `${ResourceName.STATS_OVERALL_VIEW}/${id}/show`
          : undefined
      }
    >
      <TextField source="id" />
      <TextField source="name" />
      <DateField source="dateStart" showTime />
      <DateField source="dateEnd" showTime />
    </Datagrid>
  )
}

export const OverallViewList = (props: ListProps) => (
  <List
    {...props}
    actions={false}
    filters={<ListFilters />}
    sort={{ field: 'id', order: 'DESC' }}
  >
    <OverallViewGrid />
  </List>
)
