import React from "react"
import {
  Show, ShowProps, TabbedShowLayout, Tab, TextField, DateField, useListContext,
  useShowContext, useTranslate, FunctionField, Record, ResourceContextProvider,
  List, useMutation, useRefresh, sanitizeFetchType, EditButton, TextInput, useNotify,
} from "react-admin"
import {
  Checkbox, TableContainer, Table, TableHead, TableBody, TableRow, TableCell, Divider, Box, Button, ButtonGroup
} from '@material-ui/core'
import { ResourceName } from "../../core/ResourceName"
import { EventStatus } from "../../core/enum/EventStatus"

const ShowTitle = (record: any) => {
  const { record: { name, dateStart, id } } = record
  const translate = useTranslate()
  return (
    <span>{translate('resources.events.titles.show')}: {!!name && name} {dateStart.replace("T", " ")} {!!id && id}</span>
  )
}

const Attachments = ({ source }: { source: 'COMPANIES' | 'OBJECTS' | 'ENGINES' | 'PALMS' }) => {
  const { data, ids } = useListContext()
  const { record: event } = useShowContext()
  const translate = useTranslate()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const notify = useNotify()

  const handleAttach = async (id: number[]) => {
    switch (source) {
      case 'COMPANIES':
        await mutate({
          type: sanitizeFetchType("attachCompanies"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            companiesIds: id
          }
        }, { returnPromise: true })
      break;
      case 'OBJECTS':
        await mutate({
          type: sanitizeFetchType("attachObjects"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            objectsIds: id
          }
        }, { returnPromise: true })
      break;
      case 'ENGINES':
      case 'PALMS':
        await mutate({
          type: sanitizeFetchType("attachDevices"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            verifierDevicesIds: id
          }
        }, { returnPromise: true })
      break;
      default:
        notify(`Source '${source}' is not implemented yet.`);
    }
    refresh()
  }

  const handleDetach = async (id: number[]) => {
    switch (source) {
      case 'COMPANIES':
        await mutate({
          type: sanitizeFetchType("detachCompanies"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            companiesIds: id
          }
        }, { returnPromise: true })
      break;
      case 'OBJECTS':
        await mutate({
          type: sanitizeFetchType("detachObjects"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            objectsIds: id
          }
        }, { returnPromise: true })
      break;
      case 'ENGINES':
      case 'PALMS':
        await mutate({
          type: sanitizeFetchType("detachDevices"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            verifierDevicesIds: id
          }
        }, { returnPromise: true })
      break;
      default:
        notify(`Source '${source}' is not implemented yet.`);
    }
    refresh()
  }

  return (
    <TableContainer>
      <ButtonGroup>
        <Button variant="contained" onClick={() => handleAttach(ids as number[])}>
          {translate('resources.events.actions.attachAll')}</Button>
        <Button variant="contained" onClick={() => handleDetach(ids as number[])}>
          {translate('resources.events.actions.detachAll')}</Button>
      </ButtonGroup>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              {translate(`resources.events.fields.attachment.name`)}
            </TableCell>
            <TableCell style={{ width: "10%" }}>
              {translate(`resources.events.fields.attachment.attached`)}
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <TableRow key={id}>
              <TableCell>
                <TextField source="name" record={data[id]} />
              </TableCell>
              <TableCell>
                <Checkbox checked={data[id]?.attached} onClick={data[id]?.attached ? () => handleDetach([id as number]) : () => handleAttach([id as number])} />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const GeneralEditButton = () => {
  const { record } = useShowContext()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleActivation = async () => {
    if (record?.status !== EventStatus.IN_TRANSITION) {
      if (record?.status === EventStatus.ACTIVE){
        await mutate({
          type: sanitizeFetchType("deactivate"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: record?.id
          }
        }, { returnPromise: false })
      }
      else {
        await mutate({
          type: sanitizeFetchType("activate"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: record?.id
          }
        }, { returnPromise: false })
      }
      refresh()
    }
  }

  return (
    <>
      <Divider />
      <Box p={1} display="flex" justifyContent="flex-end">
        <Button variant="contained" onClick={handleActivation}
          color="primary" style={{ marginRight: "5px" }}
          disabled={record?.status === EventStatus.IN_TRANSITION}>
          {record?.status === EventStatus.INACTIVE ?
            translate('resources.events.actions.activate') :
            translate('resources.events.actions.deactivate')}
        </Button>
        <EditButton basePath="/events" record={record} variant="contained" />
      </Box>
    </>
  )
}

export const EventShow = (props: ShowProps) => {
  const translate = useTranslate()
  const { id, basePath } = props
  return (
    <Show {...props} title={<ShowTitle />} actions={false}>
      <TabbedShowLayout>
        <Tab label={translate("resources.events.tabs.general")}>
          <TextField source="id" label="ID" />
          <TextField source="name" />
          <DateField source="dateStart" showTime />
          <DateField source="dateEnd" showTime />
          <FunctionField resource="events" label={translate("resources.events.fields.status")}
            render={(r: Record | undefined) =>
              (translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))} />
          <GeneralEditButton />
        </Tab>
        <Tab label={translate("resources.events.tabs.palms")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.PALMS} >
                <List basePath={basePath} filter={{ event: id }} exporter={false} title=" "
                  filters={[
                    <TextInput source="name" alwaysOn />,
                    <TextInput source="categoryId" />
                  ]}>
                  <Attachments source="PALMS" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
        <Tab label={translate("resources.events.tabs.engines")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.ENGINES}>
                <List basePath={basePath} filter={{ event: id }} exporter={false} title=" "
                  filters={[
                    <TextInput source="name" alwaysOn />,
                    <TextInput source="categoryId" />
                  ]}>
                  <Attachments source="ENGINES" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
        <Tab label={translate("resources.events.tabs.objects")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.OBJECTS}>
                <List basePath={basePath} filter={{ event: id }} exporter={false} title=" "
                  filters={[
                    <TextInput source="name" alwaysOn />
                  ]}>
                  <Attachments source="OBJECTS" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
        <Tab label={translate("resources.events.tabs.companies")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.COMPANIES}>
                <List basePath={basePath} filter={{ event: id }} exporter={false} title=" "
                  filters={[
                    <TextInput source="name" alwaysOn />
                  ]}>
                  <Attachments source="COMPANIES" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
      </TabbedShowLayout>
    </Show>
  )
}