import { BookingDto } from '../dto/booking.dto'

const mapping: Partial<
  Record<keyof BookingDto, (lambdaParameter: string, value: string | number) => string>
> = {
  id: (lambdaParameter, value) => value?.toString().split(",").length > 1 ?
    `"${value}".Contains(${(lambdaParameter)}.Id.ToString())` :
    `${(lambdaParameter)}.Id==${value}`,
}

export const getFilterMapper = (key: keyof BookingDto) =>
  mapping[key] as (lambdaParameters: string, value: string | number) => string