import {
  DataProvider,
  GetListResult,
  GetOneParams,
  GetOneResult,
} from 'react-admin'
import { ACCESSES_URL, USERS_URL } from '../api-urls'
import { getWithPagination, get } from '../common/fetch.utils'
import { AccessDto } from '../dto/access.dto'
import { UserDto } from '../dto/user.dto'
import { Access } from './access'
import { getFilterMapper } from './access-filter.mapper'
import { mapSortAccessParam } from './access-sort.mapper'
import { AccessParams } from './access.params'

const getFilterParams = (filter: Partial<Access>) => {
  if (Object.keys(filter).length) {
    const filterParams = Object.entries(filter).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof Access)
      return mapper('a', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    {
      filter,
      sort: { field, order },
      pagination: { page, perPage },
    }: AccessParams,
  ): Promise<GetListResult<Access>> => {
    const filterParams = `a=>${getFilterParams(filter).toString()}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortAccessParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const { data } = await getWithPagination<AccessDto[]>(
      `${ACCESSES_URL}/GetByConditions`,
      path,
    )
    const accesses = data.map(
      ({
        id,
        name,
        bonus,
        entranceId,
        allowReenter,
        multipleEntry,
        multiple,
        usePriority,
        isTicketUnlockWithNextDay,
        releaseTime,
        releaseDayOfWeek,
        releaseDayOfMonth,
        delay,
        activePeriod,
        activePeriodStartingFrom,
        variantsIds,
        accessDatesIds,
        createdById,
        modifiedById,
        createdAt,
        modifiedAt,
      }) =>
        ({
          id,
          name,
          bonus,
          entranceId,
          allowReenter,
          multipleEntry,
          multiple,
          usePriority,
          isTicketUnlockWithNextDay,
          releaseTime,
          releaseDayOfWeek,
          releaseDayOfMonth,
          delay,
          activePeriod,
          activePeriodStartingFrom,
          variantsIds,
          accessDatesIds,
          createdById,
          modifiedById,
          createdAt,
          modifiedAt,
        } as Access),
    )

    return Promise.resolve({
      data: accesses,
      total: accesses.length,
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams,
  ): Promise<GetOneResult<Access>> => {
    const access = await get<AccessDto>(ACCESSES_URL, `/${id}`)
    const modifiedBy = await get<UserDto>(
      USERS_URL,
      access.modifiedById.toString(),
    )
    const createdBy =
      access.modifiedById === access.createdById
        ? modifiedBy
        : await get<UserDto>(USERS_URL, access.createdById.toString())
    return {
      data: {
        ...access,
        modifiedByName: modifiedBy.userName,
        createdByName: createdBy.userName,
      },
    }
  },
} as DataProvider

export default provider
