import {
  CreateParams,
  CreateResult,
  DataProvider,
  UpdateManyParams,
  UpdateManyResult,
  GetListResult
} from 'react-admin'
import { AccessUsingsCreateParams } from './access-usings-create.params'
import { AccessUsingDto } from '../dto/access-using.dto'
import { DeviceDirection } from '../enum/DeviceDirection'
import { DateTime } from '../common/date-time'
import { getByConditions, getWithPagination, patch, post } from '../common/fetch.utils'
import { ACCESS_USINGS, TICKETS_URL } from '../api-urls'
import { AccessUsingsListParams } from './access-usings-list.params'
import { mapSortAccessUsingsParam } from './access-usings-sort.mapper'
import { getFilterMapper } from './access-usings-filter.mapper'
import { TicketDto } from '../dto/ticket.dto'
import { AccessUsing } from './access-using'

const getFilterParams = (filter: Partial<AccessUsingDto>) => {
  if (Object.keys(filter).length) {
    const filterParams = Object.entries(filter).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof AccessUsingDto)
      return mapper("au", value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return "true"
}

type CreateAccessUsingRequest = Array<
  Omit<AccessUsingDto, 'id' | 'verifierDeviceId'> & { verifierDeviceId: null }
>
type UpdateAccessUsingRequest = Pick<AccessUsingDto, 'id'> & { active: false }

const provider = {
  updateMany: async (
    resource,
    { ids, data: { active = false } }: UpdateManyParams,
  ): Promise<UpdateManyResult> => {
    const pendingUpdates = ids
      .map((auId) => ({ id: auId, active } as UpdateAccessUsingRequest))
      .map((req) =>
        patch<UpdateAccessUsingRequest, AccessUsingDto>(ACCESS_USINGS, req),
      )
    const accessUsings = await Promise.all(pendingUpdates)
    return Promise.resolve({
      data: accessUsings.map((au) => au.id),
    })
  },
  create: async (
    resource,
    { data }: CreateParams<AccessUsingsCreateParams>,
  ): Promise<CreateResult> => {
    const { ticketIds, accessIds } = data
    const request: CreateAccessUsingRequest = accessIds.flatMap((accessId) =>
      ticketIds.map((ticketId) => ({
        date: new Date().toISOString() as DateTime,
        direction: DeviceDirection.IN,
        active: true,
        accessId,
        ticketId,
        verifierDeviceId: null,
      })),
    )
    await post<CreateAccessUsingRequest, AccessUsingDto[]>(
      ACCESS_USINGS,
      request,
    )
    return Promise.resolve({
      data: { id: 0 }, // need to return something
    })
  },
  getList: async (
    resource: string,
    {
      filter,
      sort: { field, order },
      pagination: { page, perPage }
    }: AccessUsingsListParams
  ): Promise<GetListResult<AccessUsingDto>> => {
    const filterParams = `au=>${getFilterParams(filter).toString()}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortAccessUsingsParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
      range: { total }
    } = await getWithPagination<AccessUsingDto[]>(`${ACCESS_USINGS}/GetByConditions`, path)

    const tickets = await getByConditions<TicketDto>
      (TICKETS_URL, `t=>${filter?.ticketId?.toString().split(" ").map((tId) => `t.Id==${tId}`).join("||")}`)

    const accessUsings = data.map(({
      id, date, direction, active, ticketId, accessId, verifierDeviceId
    }) => ({
      id, date, direction, active, ticketId, accessId, verifierDeviceId,
      ticketCode: tickets.find((ticket) => ticket.id === ticketId)?.ticketCode
    } as AccessUsing))

    return Promise.resolve({
      data: accessUsings,
      total
    })
  }
} as DataProvider

export default provider
