import ViewComfyIcon from '@material-ui/icons/ViewComfy'
import PersonIcon from '@material-ui/icons/Person'
import React from 'react'
import { Resource } from 'react-admin'
import ReceiptIcon from '@material-ui/icons/Receipt'
import { ResourceName } from '../core/ResourceName'
import { TicketInfoList } from './ticket-info/TicketInfoList'
import { TicketDetailsView } from './ticket-details/TicketDetailsView'
import { LogList } from './logs/LogList'
import { OverallViewList } from './stats/overall-view/OverallViewList'
import { OverallView } from './stats/overall-view/OverallView'
import { AccessUsingsList } from './ticket-details/accesses/AccessUsingsList'
import { EventList } from './events/EventList'
import { EventShow } from './events/EventShow'
import { EventEdit } from './events/EventEdit'
import { CustomerInfoList } from './customer-info/CustomerInfoList'
import { CustomerDetailsView } from './customer-details/CustomerDetailsView'
import { Authority } from '../core/auth/Authority'
import { hasAuthority } from './hooks/useHasAuthority'

export const generateResourcesBasedOnPermissions = (permissions: Set<string>) => [
  <Resource
    icon={ReceiptIcon}
    name={ResourceName.TICKET_INFO}
    list={TicketInfoList}
  />,
  <Resource name={ResourceName.TICKET_DETAILS} show={TicketDetailsView} />,
  <Resource name={ResourceName.ACCESS_USINGS} list={AccessUsingsList} />,
  <Resource
    icon={PersonIcon}
    name={ResourceName.CUSTOMER_INFO}
    list={CustomerInfoList}
  />,
  <Resource name={ResourceName.CUSTOMER_DETAILS} show={CustomerDetailsView} />,
  hasAuthority(Authority.VIEW_LOGS, permissions) && (
    <Resource name={ResourceName.LOGS} list={LogList} />
  ),
  <Resource
    name={ResourceName.EVENTS}
    list={EventList}
    show={EventShow}
    edit={EventEdit}
  />,
  <Resource
    icon={ViewComfyIcon}
    name={ResourceName.STATS_OVERALL_VIEW_LIST}
    list={OverallViewList}
  />,
  hasAuthority(Authority.VIEW_STATS_OVERALL_VIEW, permissions) && (
    <Resource
      icon={ViewComfyIcon}
      name={ResourceName.STATS_OVERALL_VIEW}
      show={OverallView}
    />
  ),
  <Resource name={ResourceName.ACCESS_DATES} />,
  <Resource name={ResourceName.ACCESSES} />,
  <Resource name={ResourceName.BOOKINGS} />,
  <Resource name={ResourceName.COMPANIES} />,
  <Resource name={ResourceName.ENGINES} />,
  <Resource name={ResourceName.OBJECTS} />,
  <Resource name={ResourceName.PALMS} />,
  <Resource name={ResourceName.TICKET_LOGS} />,
]
