import { AccessUsingDto } from "../dto/access-using.dto"

const mapping: Partial<
  Record<keyof AccessUsingDto, (lambdaParameter: string, value: string | number) => string>
> = {
  id: (lambdaParameter, value) => `${(lambdaParameter)}.Id.ToString().Contains("${value}")`,
  ticketId: (lambdaParameter, value) => `"${value}".Contains(${(lambdaParameter)}.TicketId.ToString())`,
  accessId: (lambdaParameter, value) => `${(lambdaParameter)}.AccessId.ToString().Contains("${value}")`,
}

export const getFilterMapper = (key: keyof AccessUsingDto) =>
  mapping[key] as (lambdaParameter: string, value: string | number) => string
