import { ObjectDto } from '../dto/object.dto'
import { ObjectType } from './object'

export const mapToObject = (
  {
    id,
    name,
    tribunesIds,
    eventsIds
  }: ObjectDto,
  attached: number[]
): ObjectType => ({
  id,
  name,
  tribunesIds,
  eventsIds,
  attached: attached.includes(id)
})