import {
  Card,
  CardContent,
  CardHeader,
  createStyles,
  Grid,
  makeStyles,
  Theme,
} from '@material-ui/core'
import React from 'react'
import { useSelector } from 'react-redux'
import { ResourceContextProvider, useTranslate } from 'react-admin'
import { ResourceName } from '../core/ResourceName'
import { FindUserByTicket } from './common/FindUserByTicket'
import { EventList } from './events/EventList'
import { LogList } from './logs/LogList'
import { DashboardItem, DashboardState } from '../core/common/dashboard'
import { useHasAuthority } from './hooks/useHasAuthority'
import { Authority } from '../core/auth/Authority'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    header: {
      textAlign: 'center',
      letterSpacing: theme.spacing(0.2),
    },
    lastLogs: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'flex-end',
      width: '10%',
    },
  }),
)

const TicketFinder = () => (
  <Card style={{ width: '100%', marginBottom: '5px' }}>
    <FindUserByTicket />
  </Card>
)

const LastLogs = () => {
  const t = useTranslate()
  return (
    <Card style={{ width: '100%', marginBottom: '5px' }}>
      <CardHeader title={t('dashboard.cards.last-logs.title')} />
      <CardContent>
        <ResourceContextProvider value={ResourceName.LOGS}>
          <LogList
            basePath="/"
            title=" "
            compactMode
            pagination={false}
            perPage={10}
            actions={false}
            filters={undefined}
          />
        </ResourceContextProvider>
      </CardContent>
    </Card>
  )
}

const LastEvents = () => {
  const t = useTranslate()
  return (
    <Card style={{ width: '100%', marginBottom: '5px' }}>
      <CardHeader title={t('dashboard.cards.last-events.title')} />
      <CardContent>
        <ResourceContextProvider value={ResourceName.EVENTS}>
          <EventList
            sort={{ field: 'status', order: 'DESC' }}
            compactMode
            basePath="/"
            title=" "
          />
        </ResourceContextProvider>
      </CardContent>
    </Card>
  )
}

export const Dashboard = () => {
  const classes = useStyles()
  const t = useTranslate()
  const hasAuthority = useHasAuthority()
  const inactiveComponents = useSelector(
    (state: DashboardState) => state.dashboard.inactiveComponents,
  )
  const isItemActive = (item: DashboardItem, inactiveItems: DashboardItem[]) =>
    !inactiveItems.includes(item)

  return (
    <Grid container spacing={1}>
      <Grid item xs={12}>
        <Card>
          <CardHeader
            className={classes.header}
            title={t('dashboard.welcome')}
            titleTypographyProps={{ variant: 'h4' }}
          />
        </Card>
      </Grid>
      <Grid item sm={6} xs={12}>
        {isItemActive(DashboardItem.TICKET_FINDER, inactiveComponents) && (
          <TicketFinder />
        )}
        {isItemActive(DashboardItem.LAST_EVENTS, inactiveComponents) && (
          <LastEvents />
        )}
      </Grid>
      <Grid className={classes.lastLogs} item sm={6} xs={12}>
        {hasAuthority(Authority.VIEW_LOGS) &&
          isItemActive(DashboardItem.LAST_LOGS, inactiveComponents) && (
            <LastLogs />
          )}
      </Grid>
    </Grid>
  )
}
